.Label > button {
  display: flex;
  flex-direction: row;
  padding: 4px 16px 4px 16px;
  gap: 0 8px;
  align-items: center;
  height: 100%;
  border-radius: 4px;
  background: rgba(148, 151, 154, 0.1);
}
.Label img {
  width: 20.8px;
  height: 20.8px;
}
.Label p {
  color: #ffffff;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 0.75px;
  margin-top: 5%;
  white-space: nowrap;
}
